body {
  font-family: sans-serif;
  align-items: center;
  padding: 50px 0;
}
#cal {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 10px solid grey;
  box-sizing: border-box;
  background-color: black;
  color: whitesmoke;
}
#calculator {
  margin: 17vh auto;
  background-color: white;
  width: 50%;
  height: 70%;
  min-width: 500px;
  max-width: 400px;
  border: 10px solid grey;
  box-shadow: 0 0 6px black;
  transform: scale(1.2);
}

#display-container {
  font-size: 3 rem;
  color: black;
  width: 100%;
  height: 20%;
  border: 5px solid grey;
  display: flex;
  justify-content: center;
}

.buttons {
  width: 23%;
  heigth: 15%;
  display: inline-block;
  border: 1px solid grey;
  font-size: 1.8rem;
  justify-content: space-between;
  margin-left: 2px;
  text-align: center;
  transition: all 0.3s;
}
.buttons:hover {
  background-color: #7b7878;
  box-shadow: 0 0 5px lightyellow;
}

.buttons[data-value="/"] {
  background-color: #c87f7f;
}
.buttons[data-value="*"] {
  background-color: #c87f7f;
}
.buttons[data-value="-"] {
  background-color: #c87f7f;
}
.buttons[data-value="+"] {
  background-color: #c87f7f;
}
.buttons[data-value="="] {
  background-color: #c87f7f;
}

/* Arithmatic Operators have a separate background color  */
